@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
}

.navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar-logo {
    margin-right: 2rem;
}

.navbar-logo .gradient__text {
    font-weight: 500;
}

.navbar-links_container {
    display: flex;
    flex-direction: row;
}

.navbar-connectW {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar-links_container span {
    margin: 1rem 0 0 5rem;
}

.navbar-links_container p,
.navbar-connectW p,
.navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-size: 17px;
    font-weight: 500;
    line-height: 25px;
    text-transform: capitalize;

    margin: 1rem 2rem 1rem 0;
    cursor: pointer;
}

.navbar-connectW button:hover,
.navbar-links_container a:hover,
.navbar-menu_container-links a:hover,
.navbar-menu_container-links-connectW button:hover {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.navbar-connectW button,
.navbar-menu_container button {
    padding: 0.5rem 1rem;
    margin-left: 0.5rem;
    color: #fff;
    background-color: #FF4820;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 500;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.navbar-menu {
    margin: 1rem;
    
    display: none;
    position: relative;
}

.navbar-menu svg {
    color: #fff;
    cursor: pointer;
}

.navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2)
}

.navbar-menu_conatainer p {
    margin: 1rem 0;
}

.navbar-menu_container-links-connectW {
    display: none;
}

@media screen and (max-width: 1050px) {
    .navbar-links_container {
        display: none;
    }

    .navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .navbar {
        padding: 2rem;
    }

    .navbar-connectW {
        display: none;
    }

    .navbar-menu_container {
        top: 20px;
    }

    .navbar-menu_container-links-connectW {
        display: block;
    }
}
.header {
    display: flex;
}

.header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 46.5px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .header {
        flex-direction: column;
    }
    .header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .header h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .header p {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .header h1 {
        font-size: 36px;
        line-height: 48px;
    }
    .header p {
        font-size: 14px;
        line-height: 24px;
    }
}




* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    /* ff 3.6+ */ 

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */
    background:-o-linear-gradient(80deg, rgba(4, 30, 59, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#040C18', endColorstr='#041E3B', GradientType=1 );

    /* ie 10+ */

    /* global 94%+ browsers support */
    background:linear-gradient(80deg, rgba(4, 30, 59, 1) 0%, rgba(4, 12, 24, 1) 100%);
}

a {
    color: unset;
}

.gradient__bg {

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgba(0, 42, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes scale-up-center {
0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}


@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }
    
    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }
    
    .section__margin {
        margin: 4rem 2rem;
    }
}
